<template>
  <div class="am-g">
    <!--  -->
    <header class="am-topbar am-topbar-inverse" style="margin-bottom: 0rem!important;">
      <div class="am-container">
        <h4 class="am-topbar-brand am-fl">
                <img src="./../../img/logoss.png" style="max-width: 111px;"/>
                <a href="/" style="vertical-align: middle;"></a>
            </h4>
        <button class="am-topbar-btn am-topbar-toggle am-btn am-btn-sm am-btn-success am-show-sm-only" @click="toggleNav()"><span class="am-sr-only"></span>
          <span class="am-icon-bars"></span>
        </button>
        <div class="am-collapse am-topbar-collapse am-fr" v-bind:class="{ 'am-in': isNavOpen}" v-show="isNavOpen" transition="slideNav">
          <ul class="am-nav am-topbar-nav am-nav-pills">
            <li class="topIcon">
              <a href="#"></a>
              <ul class="am-show-md-up">
                <li><a href="http://spm.ncu.edu.cn/static/Instruction.asp"></a></li>
                <li><a href="http://spm.ncu.edu.cn/static/xueyuanlingdao.asp"></a></li>
                <li><a href="http://spm.ncu.edu.cn/Static/Structure.asp"></a></li>
                <li style="padding-bottom: 2px;"><a href="#"></a></li>
              </ul>
            </li>
            <li class="topIcon">
              <a href="#"></a>
              <ul>
                <li><a href="#"></a></li>
                <li><a href="#"></a></li>
                <li><a href="http://mpa.ncu.edu.cn/" style="font-size: 13px;">MPA</a></li>
                <li style="padding-bottom: 2px;"><a href="http://cacs.ncu.edu.cn/"></a></li>
              </ul>
            </li>
            <li class="topIcon">
              <a href="#"></a>
            </li>
            <li class="topIcon">
              <a href="#"></a>
              <ul>
                <li><a href="http://spm.ncu.edu.cn/Static/Master.asp"></a></li>
                <li style="padding-bottom: 2px;"><a href="#"></a></li>
              </ul>
            </li>
            <li class="topIcon">
              <a href="#"></a>
              <ul>
                <li><a href="#"></a></li>
                <li style="padding-bottom: 2px;"><a href="http://spm.ncu.edu.cn/static/NewsList.asp?Type=3" style="font-size: 12px;"></a></li>
              </ul>
            </li>
            <li class="topIcon">
              <a href="http://spm.ncu.edu.cn/static/MPAEdu.asp">MPA</a>
            </li>
            <li class="topIcon">
              <a href="#" @click="showLogin()" v-if="showLoginButton">/</a>
              <a href="#" v-if="isLogin"></a>
            </li>
          </ul>
        </div>
      </div>
    </header>
  </div>
</template>
<script>
var $ = require('jquery');
require('./../css/header.css');

export default {
  data() {
      return {
        showLoginButton: true,
        isLogin: false,
        isNavOpen: false
      }
    },
    created() {
      var that = this;
      $.ajax({
        url: "/user/info",
        type: 'get',
        dataType: 'json',
        success: function(data) {
          console.log(data);
          that.isLogin = true;
          that.showLoginButton = false;
        },
        error: function(err) {
          console.log(err.responseText);
          that.isLogin = false;
          that.showLoginButton = true;
        }
      })
    },
    ready() {
      $('.topIcon').hover(function() {
        $(this).children('ul').css("visibility", "visible");
      }, function() {
        $('.topIcon ul').css("visibility", "hidden")
      });
    },
    methods: {
      showLogin: function() {
        console.log('Start Dispatch')
        this.$dispatch("toggleLogin");
      },
      toggleNav: function() {
        console.log("Start Toggle")
        this.isNavOpen = !this.isNavOpen;
      }
    }
}
</script>
<style lang="sass">
.slideNav-transition {
  transition: 0.18s ease-out;
  height: 225px;
  overflow: hidden!important;
}

.slideNav-enter,
.slideNav-leave {
  height: 0px;
  opacity: 0;
  overflow: hidden!important;
}
</style>



